body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  display: flex;
  justify-content: center;
 background-image: linear-gradient(90deg,#705fac,#2d2174 );
}

.App{
  height: 500px;
  width: 300px;
margin-top: 50px;

padding: 5px 10px;
display: flex;
flex-direction: column;
justify-content: space-around;
align-items: center;
border-radius: 10px;
  background-color:rgb(50, 50, 185);
box-shadow: 0 0 4px 4px black;
}

.outeri{
height: 48%;
width: 99%;
border: 3px solid black;
background-color: rgb(160, 158, 158);
display: flex;
justify-content: center;
align-items: center;
position: relative;
}
.outer{
  height: 48%;
width: 99%;

background-color:rgb(50, 50, 185);
display: flex;
justify-content: center;
align-items: center;
position: relative;
}

.outer-wheel{
  height: 200px;
width: 200px;
background-color: rgb(224, 221, 221);
border-radius: 50%;
display: flex;
justify-content: center;
align-items: center;
position: relative;
}
.inner-wheel{
  height: 60px;
  width: 60px;
  background-color:rgb(70, 67, 67);
  border-radius: 50%;
}

.menu{
  user-select: none;
position: absolute;
font-size: 1.5rem;
height:35px;
left: 30%;
top:6%;
padding: 6px;
display: flex;
align-items: center;
font-weight: bold;
}
.effect:hover{
  border-radius: 50%;
  background-color:rgb(50, 50, 185);
}
.forward{
  user-select: none;
  position: absolute;
width: 38px; ;
height:35px;
left:72%;
top:36%;
padding: 6px;

display: flex;
align-items: center;
}

.backward{
  user-select: none;
  position: absolute;
  width: 38px; ;
  height:35px;
  left:5%;
  top:36%;
  padding: 6px;

  display: flex;
  align-items: center;
}
.play-pause{
  user-select: none;
  position: absolute;
  width: 38px; ;
  height:35px;
  left:37%;
  top:70%;
  padding: 6px;

  display: flex;
  align-items: center;
}
.hidden-menu{
  
  position: absolute;
  width: 60%;
  height: 100%;
  left: 0;
  margin: 0;
  text-align: center;
   line-height: 40px;
 
  background-color: white;
  font-weight: bold;
  box-shadow: 2px 0 3px 3px rgb(15, 14, 14);
  
  
}
.hidden-menu2{
  
  position: absolute;
  width: 60%;
  height: 100%;
  left: 0;
  margin: 0;
  text-align: center;
   line-height: 40px;
 visibility: hidden;
  background-color: white;
  font-weight: bold;
  box-shadow: 2px 0 3px 3px rgb(15, 14, 14);
  
  
}
.inner-wheel:hover{
  opacity: .5;
}

.sub-game{
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  visibility: hidden;
}
.sub-game img{
  height: 50%;
  width: 50%;
  border-radius: 50%;
}
.sub-cover{
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: white;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  visibility: hidden;
}
.sub-cover img{
  height: 50%;
  width: 50%;
  border-radius: 50%;
  border:1px solid black;
}
.sub-cover audio{
  width: 90%;
  outline: none;
  
}
.time{
  position: absolute;
  width: 100%;
 
  height: 30%;
  left: 0;
  top:0;
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
  font-size: 4rem;
  
}
.menu-items{
  width: 60%;
  height: 100%;
  list-style: none;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  border: 1px solid black;
}
.active{
  background-color:#2d2174;
  color: white;
}


img{
  width: 100%;
  height: 100%;
}